import Vue from 'vue'
import VueI18n from 'vue-i18n'
import router from './router'

import App from './App.vue'

import ko_KR from './locales/ko-KR'

import $ from 'jquery'
global.jQuery = global.$ = $;

import 'bootstrap'
import GlobalConst from './globalconst'

import VoiceList from './voices.json'


//提取标签到语言文件
let addko_KR = { voice: {}, voicecategory: {} };

for (let voiceCategoryList of VoiceList.voices){
  if(voiceCategoryList.categoryDescription !== undefined){
    if(voiceCategoryList.categoryDescription['ko-KR'] !== undefined){
      addko_KR.voicecategory[voiceCategoryList.categoryName] = voiceCategoryList.categoryDescription['ko-KR'];
    }
  }
  for (let voiceItem of voiceCategoryList.voiceList){
    if(voiceItem.description !== undefined){
      if(voiceItem.description['ko-KR'] !== undefined){
        addko_KR.voice[voiceItem.name] = voiceItem.description['ko-KR'];
      }
    }
  }
}

let emko_KR = Object.assign(ko_KR, addko_KR);

Vue.config.productionTip = false

Vue.use(VueI18n)
Vue.use(GlobalConst);

const messages = {
  'ko-KR': emko_KR
}

let locale = 'ko-KR';

const i18n = new VueI18n({
  locale,
  messages
})


new Vue({
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
